gicos dopaminérgicos, que están asociados al placer de aprender. Cuando el entorno refuerza una actitud positiva hacia el conocimiento, se favorece la construcción de hábitos intelectuales, la autonomía personal y el desarrollo cognitivo.")]),_c('div',{staticClass:"bg-color-1 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema2/15.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Asimismo, el acceso a espacios adecuados, materiales didácticos y programas extracurriculares depende en gran medida de las condiciones comunitarias. Comunidades con infraestructura sólida y redes activas abren oportunidades para la exploración, el juego y el aprendizaje colaborativo. Como menciona Susana (2024), “la forma en que los estudiantes se comunican y se relacionan en el aula está profundamente marcada por las normas culturales que han interiorizado”.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center bg-color-5 mb-5"},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-9 mb-3 mb-lg-0 order-2",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Estos autores, citando a la Unesco, afirman que “la familia es la unidad fundamental de la sociedad y, por lo tanto, tiene un papel crucial en la transmisión de valores culturales y éticos en el proceso de desarrollo”. Además, Unicef, a través de la Convención sobre los Derechos del Niño (1989), declara que “la familia es el entorno natural para el desarrollo de los niños”.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Hoy en día, la familia se concibe como una organización donde todos sus miembros deben participar activamente en la formación d