ctamente en el acceso a recursos educativos: libros, dispositivos tecnológicos, clases extracurriculares. Sin embargo, el potencial de aprendizaje no está determinado exclusivamente por lo económico. Muchas familias de bajos ingresos desarrollan estrategias creativas de apoyo educativo, aprovechando recursos comunitarios, bibliotecas públicas o espacios culturales.")])])])]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Desde el plano cultural, los valores y normas familiares condicionan el enfoque hacia la educación. En algunos grupos, se otorga un alto valor al rendimiento académico, lo que genera expectativas elevadas y una mayor motivación. En otros contextos, se priorizan aspectos distintos (familia, religión, trabajo), lo que puede representar un reto para la continuidad escolar si no se integran culturalmente los procesos educativos. Los factores familiares que influyen en el aprendizaje son: ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center bg-color-12 mb-5"},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-9 mb-3 mb-lg-0 order-2",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("En este sentido, el aprendizaje de la lengua materna destaca como proceso esencial. Para Medina y Fernández (2017), la lengua es “un acceso a la cultura y un componente esencial de la nacionalidad”, y su dominio requiere el acompañamiento familiar, no solo para mejorar habilidades comunicativas, sino también para integrarse al conocimiento acumulado por la humanidad.")]),_c('p',{staticClass:"mb-0"},[_vm._v("La familia y la escuela son complementos educativos: ambas satisfacen necesidades culturales, sociales y personales. Su colaboración activa permite la construcción de hábitos de estudio, fortalece los vínculos afectivos y promueve el desarrollo integral del estudiante.")])]),_c('